.advantages {
  background-color: #f5f5f5;
  padding: 300px 0 120px;
}

.advantages__wrapper {
  display: flex;
}

.advantages__content {
  margin-right: 60px;
  max-width: 642px;
}

.advantages__title {
  margin-bottom: 15px;
}

.advantages__subtitle {
  margin-bottom: 40px;
}

.advantages__text {
  font-weight: 300;
  font-size: 22px;
  line-height: lh(22,36);

  p {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.advantages__images {
  max-width: 435px;
  width: 100%;
}

.advantages__image {
  border: 8px solid #ffffff;
  border-radius: 10px;
  height: 200px;
  position: relative;
  width: 370px;

  &:nth-child(1) {
    transform: rotate(-10deg) translate(8px,32px);
  }

  &:nth-child(2) {
    margin-left: auto;
    transform: translateY(-50px);
    z-index: 1;
  }

  &:nth-child(3) {
    transform: rotate(10deg) translate(-5px, -112px);
    z-index: 2;
  }
}

.advantages__cards {
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));

  .options__card {
    background-color: #f5f5f5;
    padding: 50px 25px 40px;

    &:last-child {
      flex-direction: row;
      grid-column: 1 / -1;

      .options__cardImg {
        margin: 0 40px 0 80px;
      }

      .options__cardText {
        height: auto;
        margin: 15px 0 25px;
      }
    }
  }

  .options__cardNum {
    font-size: 20px;
    line-height: lh(20,29);
  }

  .options__cardImg {
    margin-bottom: 40px;
    height: 80px;
    width: 80px;
  }

  .options__cardText {
    font-size: 20px;
    line-height: lh(20,28);
    min-width: 242px;
  }

  .options__cardCircle {
    background-color: #ffffff;
  }
}


@mixin advantages-1500 {

  .advantages {
    padding: 120px 0;
  }

}


@mixin advantages-992 {

  .advantages {
    padding: 50px 0;
  }

  .advantages__wrapper {
    flex-direction: column;
  }

  .advantages__content {
    margin: 0 0 50px 0;
  }

  .advantages__images {
    margin-bottom: -30px;
  }

}


@mixin advantages-576 {


  .advantages__content {
    text-align: center;
  }

  .advantages__title {
    margin-bottom: 5px;
  }

  .advantages__subtitle {
    margin-bottom: 35px;

    span {
      display: none;
    }
  }

  .advantages__text {
    font-size: 18px;
    line-height: lh(18,32);

    p {
      margin-bottom: 35px;
    }
  }


  .advantages__images {
    margin-bottom: -40px;
    max-width: 345px;
  }

  .advantages__image {
    height: 150px;
    width: 280px;

    &:nth-child(1) {
      transform: rotate(-10deg) translate(8px,10px);
    }

    &:nth-child(3) {
      transform: rotate(10deg) translate(-5px, -100px);
    }
  }

  .advantages__cards {

    .options__card {

      &:last-child {
        flex-direction: column;

        .options__cardImg {
          margin: 0 0 25px;
        }

        .options__cardText {
          margin: 0;
        }
      }
    }
  }

}