.village {
  padding: 120px 0;
}

.village__title {
  margin-bottom: 80px;
  text-align: center;
}

.village__options {
  grid-template-columns: repeat(auto-fill, minmax(388px, 1fr));
  margin-bottom: 160px;
  position: relative;

  .options__card {
    background-color: #ffffff;
    padding: 50px 25px 30px;

    &:nth-child(2) {
      position: relative;
    }
  }

  .options__cardNum {
    font-size: 25px;
    line-height: lh(25,37);
  }

  .options__cardImg {
    margin-bottom: 30px;
    height: 100px;
    width: 100px;
  }

  .options__cardText {
    font-size: 18px;
    line-height: lh(18,28);
    min-width: 242px;
  }

  .options__cardCircle {
    background-color: #f5f5f5;
  }
}

.village__triagle {
  bottom: 1px;
  left: 50%;
  position: absolute;
  transform: translate(-50%, 100%);

  svg {
    height: 100%;
    width: 100%;
  }
}

.village__card {
  display: grid;
  grid-template-columns: repeat( auto-fill, minmax(auto, 574px));
  grid-gap: 10px;
  margin-bottom: 50px;

  &:last-child {
    margin-bottom: 0;
  }
}

.village__cardMain {
  height: 500px;
  padding: 30px 35px;
  position: relative;

  img {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.village__cardBody {
  position: relative;
}

.village__cardTitle {
  color: #ffffff;
  font-weight: 700;
  font-size: 26px;
  line-height: lh(26,31);
  margin-bottom: 15px;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.15);
}

.village__cardPrice {
  color: #ffffff;
  font-weight: 700;
  font-size: 36px;
  line-height: lh(36,43);
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.15);
}

.village__cardGroupFirst {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 10px;
}

.village__cardGroupMain {
 max-height: 245px;
}

.village__cardGroupSecond {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}


@mixin village-576 {

  .village {
    padding: 50px 0;
  }

  .village__title {
    margin-bottom: 40px;
  }

  .village__options {
    margin-bottom: 100px;
  }

  .village__triagle {
    left: 0;
    transform: translateY(100%);
    width: 100%;
  }

  .village__card {
    margin-bottom: 25px;
  }

  .village__card,
  .village__cardGroupFirst,
  .village__cardGroupSecond {
    grid-gap: 5px;
  }

  .village__cardGroupFirst {
    height: 78vw;
  }

  .village__cardGroupSecond,
  .village__cardGroupMain {
    height: 39vw;
  }

  .village__cardMain {
    height: 78vw;
    padding: 20px;
  }

  .village__cardTitle {
    font-size: 18px;
    line-height: lh(18,22);
    margin-bottom: 5px;
  }

  .village__cardPrice {
    font-size: 24px;
    line-height: lh(24,29);
  }

}