.box {
  background-color: #3766cc;
  padding: 120px 0 100px;
}

.box__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -15px -15px 80px;
}

.box__title {
  color: #ffffff;
  margin: 15px;
  max-width: 370px;
  text-transform: uppercase;
}

.box__list {
  color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  font-weight: 400;
  font-size: 16px;
  line-height: lh(16,24);
  margin: 8px 0;

  ul {
    margin: 7px 15px;
    max-width: 370px;
  }

  li {
    align-items: flex-start;
    display: flex;
    margin-bottom: 14px;

    &:last-child {
      margin-bottom: 0;
    }

    &::before {
      background-color: rgba(255, 255, 255, 0.6);
      border-radius: 50%;
      content: '';
      display: inline-block;
      flex-shrink: 0;
      height: 6px;
      margin: 10px 18px 0 0;
      position: relative;
      width: 6px;
    }
  }
}

.box__gallery {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: minmax(282px, 1fr) minmax(282px, 585px) minmax(282px, 1fr);
  grid-template-rows: 245px 245px;
}

.box__galleryImg {

  &:nth-child(2) {
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
  }
}



@mixin box-992 {

  .box {
    padding: 50px 0;
  }

  .box__gallery {
    grid-template-columns:  minmax(219px, 476px) minmax(219px, 1fr);
    grid-template-rows: 188px 188px;
  }

}


@mixin box-768 {

  .box__gallery {
    grid-template-columns: minmax(161px, 1fr) minmax(161px, 1fr);
    grid-template-rows: 560px 280px 280px;
    grid-gap: 6px;
  }

  .box__galleryImg {

    &:nth-child(1) {
      grid-row-start: 2;
      grid-row-end: 3;
    }

    &:nth-child(2) {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: auto;
      grid-row-end: auto;
    }
  }

}


@mixin box-576 {

  .box__title {
    margin: 10px 0;
  }

  .box__content {
    margin: -10px 0 20px;
  }

  .box__list {
    margin: 7px 0 -3px 0;

    ul {
      margin: 3px 0;
    }

    li {
      margin-bottom: 6px;
    }
  }

  .box__gallery {
    grid-template-columns: minmax(141px, 1fr) minmax(141px, 1fr);
    grid-template-rows: 272px 138px 138px;
    grid-gap: 6px;
  }

}