.select {
  padding: 120px 0;

  .slick-arrow {
    height: 40px;
    width: 40px;
    z-index: 1;

    &::before {
      content: '';
      display: inline-block;
      height: 100%;
      width: 100%;
    }
  }

  .slick-prev {
    left: 35px;

    &::before {
      background: url(/assets/src/img/select/arrowL.png) no-repeat center/contain;
    }
  }

  .slick-next {
    right: 35px;

    &::before {
      background: url(/assets/src/img/select/arrowR.png) no-repeat center/contain;
    }
  }
}

.select__header {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -15px -15px 65px -15px;
}

.select__title {
  margin: 15px;
}

.select__markers {
  align-items: center;
  display: flex;
  flex-grow: 1;
  margin: 15px 5px;
  max-width: 558px;
  justify-content: space-between;
}

.select__marker {
  font-weight: 400;
  font-size: 18px;
  line-height: lh(18,28);
  margin: 0 10px;

  &::before {
    content: '';
    display: inline-block;
    height: 10px;
    margin-right: 20px;
    position: relative;
    transform: rotate(45deg);
    width: 10px;
  }

  &:nth-child(1) {

    &::before {
      background-color: #06b436;
    }
  }

  &:nth-child(2) {

    &::before {
      background-color: #b40606;
    }
  }

  &:nth-child(3) {

    &::before {
      background-color: #007aff;
    }
  }
}

.select__sliders {
  display: flex;
  flex-wrap: wrap;
  margin: -15px;
}

.select__slider {
  height: 570px;
  margin: 15px;
  padding: 30px 35px;
  position: relative;
  width: 570px;
}

.select__sliderContent {
  position: relative;
}

.select__sliderTitle {
  color: #ffffff;
  font-weight: 700;
  font-size: 26px;
  line-height: lh(26,31);
  margin-bottom: 25px;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.15);
}

.select__sliderMarkers {
  display: flex;
  flex-wrap: wrap;
  margin: -5px;
}

.select__sliderMarker {
  color: #ffffff;
  clip-path: polygon(100% 0%, 80% 50%, 100% 100%, 0 100%, 0 0);
  font-weight: 700;
  font-size: 16px;
  line-height: lh(16,28);
  height: 28px;
  margin: 5px;
  padding-left: 7px;
  width: 50px;

  &--green {
    background-color: #06b436;
  }

  &--blue {
    background-color: #007aff;
  }

  &--red {
    background-color: #b40606;
  }
}

.select__sliderItems {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.select__sliderItem {
  height: 570px;
}


@mixin select-992 {

  .select {
    padding: 50px 0;
  }

}


@mixin select-576 {

  .select {

    .slick-arrow {
      height: 30px;
      width: 30px;
    }

    .slick-prev {
      left: 15px;
    }

    .slick-next {
      right: 15px;
    }
  }

  .select__header {
    justify-content: center;
    margin: -10px -10px  20px;
  }

  .select__title {
    margin: 10px;
  }

  .select__markers {
    margin: 10px 5px;
  }

  .select__marker {
    font-weight: 400;
    font-size: 14px;
    line-height: lh(14,20);
    margin: 0 5px;

    &::before {
      height: 6px;
      margin-right: 5px;
      width: 6px;
    }
  }

  .select__sliders {
    margin: -12px;
  }

  .select__slider {
    height: 91.1vw;
    margin: 12px;
    padding: 15px;
    width: 100vw;
  }

  .select__sliderItem {
    height: 91.1vw;
  }

  .select__sliderTitle {
    font-size: 16px;
    line-height: lh(16,19);
    margin-bottom: 12px;
  }

  .select__sliderMarkers {
    margin: -2px;
  }

  .select__sliderMarker {
    font-size: 12px;
    height: 20px;
    line-height: lh(12,20);
    margin: 2px;
    padding-left: 4px;
    width: 36px;
  }

}