.socials {
  display: flex;
  margin: 0 -5px;

  li {
    margin: 0 5px;
  }

  a {
    display: block;
    height: 40px;
    width: 40px;

    &:hover,
    &:focus {

      svg {

        path {
          fill-opacity: 1;
        }

        rect {
          stroke-opacity: 1;
        }
      }
    }
  }

  svg {
    height: 100%;
    width: 100%;

    path {
      transition: fill-opacity 0.3s;
    }

    rect {
      transition: stroke-opacity 0.3s;
    }
  }
}

@mixin socials-576 {

  .socials {
    margin: 0 -3px;

    li {
      margin: 0 3px;
    }

    a {
      height: 30px;
      width: 30px;
    }
  }

}