.map {
  height: 750px;
  position: relative;
}

.map__bg {
  bottom: 0;
  left: 0;
  position: absolute;
  top: 0;
  right: 0;
}

.map__body {
  padding-top: 85px;
  position: relative;
}

.map__subtitle {
  background-color: #ffffff;
  display: inline-block;
  margin-left: -30px;
  padding: 15px 30px;
}


@mixin map-576 {

  .map {
    height: 125vw;
  }

  .map__body {
    padding-top: 40px;
  }

  .map__subtitle {
    margin-left: -16px;
    padding: 9px 16px;
  }

}