@font-face {
  font-display: swap;
  font-family: 'Neumann';
  font-style: normal;
  font-weight: 400;
  src: url('/assets/src/fonts/Neumann.woff2') format('woff2'), /* Super Modern Browsers */
  url('/assets/src/fonts/Neumann.woff') format('woff'), /* Modern Browsers */
  url('/assets/src/fonts/Neumann.ttf') format('truetype'); /* Safari, Android, iOS */
}

