.photo {
  background: url(/assets/src/img/photo/bg.jpg) no-repeat center/cover;
  padding: 120px 0;
}

.photo__title {
  margin-bottom: 80px;
  text-align: center;
}

.photo__wrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 350px repeat(2, 1fr);
  grid-template-rows: repeat(3, 320px);
}

.photo__img {

  &:nth-child(1) {
    grid-column-start: 1;
    grid-column-end: 3;
  }

  &:nth-child(3) {
    grid-row-start: 2;
    grid-row-end: 4;
  }

  &:nth-child(6) {
    grid-column-start: 2;
    grid-column-end: 4;
  }

}


@mixin photo-992 {

  .photo {
    padding: 50px 0;
  }

  .photo__wrapper {
    grid-template-columns: 290px repeat(2, 1fr);
    grid-template-rows: repeat(3, 240px);
  }

}

@mixin photo-768 {

  .photo__wrapper {
    grid-gap: 5px;
    grid-template-columns: 225px repeat(2, 1fr);
    grid-template-rows: repeat(3, 215px);
  }

}

@mixin photo-576 {

  .photo__title {
    margin-bottom: 40px;
  }

  .photo__wrapper {
    grid-gap: 3px;
    grid-template-columns: 27.3vw repeat(2, 31.1vw);
    grid-template-rows: repeat(3, 24.8vw);
  }

}