.gallery {

}

.gallery__item {
  height: 700px;
  position: relative;

  .container {
    height: 100%;
    position: relative;
  }

  &:nth-child(1) {
    padding: 140px 0 135px;

    .gallery__itemBg {
      background: url(/assets/src/img/gallery/bg1.jpg) no-repeat center/cover;
      left: 0;
    }

    .gallery__content {
      max-width: 370px;
    }

    .gallery__slider {
      right: 0;
    }
  }

  &:nth-child(2) {
    padding: 80px 0;

    .gallery__itemBg {
      background: url(/assets/src/img/gallery/bg2.jpg) no-repeat center/cover;
      right: 0;
    }

    .gallery__content {
      margin-left: auto;
      max-width: 470px;
    }

    .gallery__slider {
      left: 0;
    }
  }

  .slider__btns {
    bottom: 0;
    left: 0;
  }
}

.gallery__itemBg {
  bottom: 0;
  position: absolute;
  top: 0;
  width: 770px;
}

.gallery__content {
  height: 100%;
  position: relative;
}

.gallery__subtitle {
  margin-bottom: 25px;
}



@mixin gallery-1500 {

  .gallery__item {
    height: auto;

    &:nth-child(1) {
      padding: 0;
    }

    &:nth-child(2) {
      padding: 0;

      .gallery__itemBg {
        left: 0;
        right: auto;
      }

      .gallery__content {
        margin-left: 0;
      }
    }

    .slider__btns {
      position: relative;
    }

  }

  .gallery__itemBg {
    width: 100%;
  }

  .gallery__content {
    padding: 50px 0 40px;
  }

  .gallery__subtitle {
    margin-bottom: 15px;
  }

  .gallery__text {
    margin-bottom: 20px;
  }

  .gallery__slider {
    bottom: 0;
    position: relative;
    width: 700px;
  }

}


@mixin gallery-768 {

  .gallery__slider {
    height: 91vw;
    width: 100vw;
  }

  .gallery {

    .slider__item {
      height: 91vw;
    }
  }

}


