.headFoot {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -15px -10px;
}

.headFoot__logo {
  margin: 10px;
}

.headFoot__socials {
  margin: 10px 15px 15px 15px;
}

.headFoot__contacts {
  margin: 15px 10px;
}

@mixin headFoot-576 {

  .headFoot__body {
    margin: -10px;
    padding: 20px 0;
  }

  .headFoot__logo {
    margin: 7px 10px;
  }

  .headFoot__socials {
    margin: 7px 15px 10px 15px;
  }

  .headFoot__contacts {
    margin: 10px;
  }

}