.hoverPhone {
  transition: transform 0.3s;

  &:hover,
  &:focus {
    transform: translateX(20px);
  }
}

.hoverBtn {
  transition: transform 0.3s;

  &:hover,
  &:focus {
    transform: scale(1.05);
  }
}

.hoverLink {
  display: inline-block;
  transition: transform 0.3s;

  &::after {
    transition: transform 0.3s;
  }

  &:hover,
  &:focus {
    transform: translateX(-10px);

    &::after {
      transform: translateX(20px);
    }
  }
}