.sendForm {

}

.sendForm__phone {
  margin-bottom: 10px;
}

.sendForm_btn {
  margin-bottom: 15px;
}

.sendForm__rules {
  color: rgba(40, 41, 46, 0.7);
  font-weight: 300;
  font-size: 14px;
  line-height: lh(14,20);
  text-align: center;

  a {
    color: rgba(40, 41, 46, 0.7);
    display: inline-block;
    position: relative;

    &::after {
      background-color: rgba(40, 41, 46, 0.7);
      bottom: 0;
      content: '';
      display: block;
      height: 1px;
      left: 0;
      position: absolute;
      width: 100%;
    }
  }
}

@mixin sendForm-576 {

  .sendForm__rules {
    font-size: 12px;
    line-height: (12,16);
  }

}