.blockBtn--1 {
  align-items: center;
  border-radius: 220px;
  color: #6e5a45;
  display: inline-flex;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.1em;
  line-height: lh(14,17);
  max-width: 370px;
  justify-content: center;
  padding: 25px 15px;
  position: relative;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 1px 1px rgba(255, 255, 255, 0.25);
  width: 100%;

  svg {
    height: 30px;
    margin-right: 25px;
    width: 30px;
    z-index: 2;
  }

  span {
    z-index: 2;
  }

  &::before {
    background: #BD9C4C;
    box-shadow: 0 10px 50px rgba(233, 200, 120, 0.5);
    border-radius: 250px;
    content: '';
    display: block;
    left: 0;
    height: 100%;
    position: absolute;
    top: 2px;
    width: 100%;
    z-index: 0;
  }


  &::after {
    background: radial-gradient(31.58% 218.14% at 50% 50%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), linear-gradient(92.7deg, #E9C878 0%, #C1A050 100%);
    border-radius: 250px;
    content: '';
    display: block;
    left: 0;
    height: 100%;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
  }
}

@mixin blockBtn1-576 {

  .blockBtn--1 {
    padding: 22px 15px;

    svg {
      height: 26px;
      margin-right: 14px;
      width: 26px;
    }

  }

}