.welcome {
  background: url(/assets/src/img/welcome/bg.jpg) no-repeat center/cover;
  padding-top: 200px;
  position: relative;

  &::before {
    background-color: rgba(55,102,204,0.8);
    bottom: 0;
    content: '';
    display: block;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0;
  }
}

.welcome__body {
  display: flex;
  justify-content: space-between;
  min-height: 650px;
  position: relative;
}

.welcome__content {

}

.welcome__title {
  margin-bottom: 20px;
  text-transform: uppercase;
}

.welcome__blockFlex {
  margin: -12px -12px 63px;
}

.welcome__subtitle {
  margin: 12px;
}

.welcome__cost {
  color: #ffffff;
  font-weight: 400;
  font-size: 18px;
  height: 50px;
  line-height: lh(18,22);
  margin: 12px;
  overflow: hidden;
  padding: 14px 15px;
  position: relative;
  width: 240px;

  svg {
    height: 100%;
    left: 0;
    top: 0;
    position: absolute;
  }

  span {
    position: relative;
  }
}

.welcome__note {
  display: flex;
  max-width: 250px;
}

.welcome__noteContainer {
  align-self: flex-end;
  background: rgba(55, 102, 204, 0.5);
  backdrop-filter: blur(2px);
  border-radius: 10px 10px 0 0;
  max-width: 250px;
  min-height: 420px;
  padding: 40px;
  position: relative;
}

.welcome__icon {
  left: -48px;
  top: -53px;
}

.welcome__noteText {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 400;
  font-size: 20px;
  line-height: lh(20,28);
  margin-bottom: 20px;
}

.welcome__noteLink {
  color: #e9c878;
  font-weight: 600;
  font-size: 18px;
  line-height: lh(18,26);
  position: relative;

  &::after {
    background-color: #e9c878;
    bottom: -2px;
    content: '';
    display: block;
    height: 1px;
    left: 0;
    position: absolute;
    width: 100%;
  }
}


@mixin welcome-1500 {

  .welcome__note {
    align-self: flex-end;
  }

  .welcome__noteContainer {
    min-height: auto;
  }

}


@mixin welcome-992 {

  .welcome__body {
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    min-height: auto;
  }

  .welcome__content {
    margin-bottom: 100px;
  }

}


@mixin welcome-576 {

  .welcome {
    padding-top: 230px;
  }

  .welcome__content {
    margin-bottom: 82px;
  }

  .welcome__title {
    margin-bottom: 10px;
    font-size: 45px;
    line-height: lh(45,66);
  }

  .welcome__blockFlex {
    margin: -7px -7px 18px;
  }

  .welcome__subtitle {
    margin: 7px;
  }

  .welcome__cost {
    font-size: 16px;
    height: 44px;
    line-height: lh(16,19);
    margin: 7px;

    svg {
      left: -14px;
    }
  }

  .welcome__noteContainer {
    max-width: 180px;
    padding: 30px;
  }

  .welcome__icon {
    left: -31px;
    top: -29px;
  }

  .welcome__noteText {
    font-size: 16px;
    line-height: lh(16,22);
    margin-bottom: 6px;
  }

  .welcome__noteLink {
    font-size: 14px;
    line-height: lh(14,20);
  }

}

