.contacts {
  align-items: flex-end;
  color: #ffffff;
  display: flex;
  flex-direction: column;
}

.contacts__title {
  font-size: 16px;
  line-height: lh(16,19);
  margin-bottom: 5px;

  span:first-child {
    font-weight: 300;
  }

  span:last-child {
    color: #e9c878;
    font-weight: 600;
  }

}

.contacts__phone {
  color: #ffffff;
  font-weight: 400;
  font-size: 26px;
  line-height: lh(26,31);
  margin-bottom: 5px;
}

.contacts__time {
  align-items: center;
  display: flex;
  font-size: 16px;
  font-weight: 300;
  line-height: lh(16,19);

  &::before {
    background-color: #14be5c;
    border-radius: 5px;
    box-shadow: 0 1px 10px rgba(20, 190, 92, 0.75);
    content: '';
    display: block;
    height: 5px;
    margin-right: 10px;
    position: relative;
    width: 5px;
  }
}

@mixin contacts-576 {

  .contacts {
    align-items: flex-start;
  }

  .contacts__title {
    font-size: 14px;
    line-height: lh(14,17);
    margin-bottom: 3px;
  }

  .contacts__phone {
    font-size: 24px;
    line-height: lh(24,29);
    margin-bottom: 2px;
  }

  .contacts__time {
    font-size: 14px;
    line-height: lh(14,17);
  }

}

