.options {
  background-color: #ebebeb;
  display: grid;
  grid-gap: 1px;
  padding: 1px;
}

.options__card {
  align-items: center;
  display: flex;
  flex-direction: column;
  position: relative;
}

.options__cardNum {
  color: #E9C878;
  font-family: Neumann, sans-serif;
  font-weight: 400;
  left: 35px;
  position: absolute;
  top: 30px;
}

.options__cardImg {
  flex-shrink: 0;
  position: relative;

  svg {
    height: 100%;
    position: relative;
    width: 100%;
  }
}

.options__cardText {
  flex-grow: 1;
  font-weight: 400;
  text-align: center;
}

.options__cardCircle {
  border-radius: 50%;
  height: 40px;
  position: absolute;
  width: 40px;

  &--rb {
    bottom: -10px;
    right: -10px
  }

  &--lb {
    bottom: -10px;
    left: -10px;
  }

  &--lt {
    left: -10px;
    top: -10px;
  }

  &--rt {
    right: -10px;
    top: -10px;
  }
}


@mixin options-576 {

  .options {
    grid-template-columns: repeat(auto-fill, minmax(286px, 1fr));
  }

  .options__card {
    padding: 40px 20px 25px;
  }

  .options__cardNum {
    left: 20px;
    top: 20px;
  }

  .options__cardImg {
    margin-bottom: 25px;
  }

  .options__cardText {
    font-size: 18px;
    line-height: lh(18,26);
  }

}