.plan {

}

.plan__body {
  position: relative;
}

.plan__img {
  overflow: auto;

  img {
    transform-origin: left top;
  }
}

.plan__imgWrapper {
  height: 750px;
  position: relative;
  width: 1540px;
}

.plan__btns {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 50px;
  top: 50%;
  transform: translateY(-50%);

  button {
    background-color: #ffffff;
    border: none;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: relative;

    &.deactivate {
      background-color: rgba(255,255,255,0.6);
    }
  }
}

.plan__btnPlus,
.plan__btnMinus {

  &::before,
  &::after {
    background-color: rgba(40, 41, 46, 0.7);
    content:'';
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    width: 12px;
  }

  &:after {
    top: 21px;
    transform: translateX(-50%) rotate(90deg);
  }
}


.plan__btnPlus {
  margin-bottom: 15px;
}

.plan__btnMinus {

  &:after {
    display: none;
  }
}

