.plan__infoMarker {
  background-color: #06b436;
  border:none;
  border-radius: 2px;
  clip-path: polygon(100% 0%, 80% 50%, 100% 100%, 0 100%, 0 0);
  color: #ffffff;
  font-weight: 700;
  font-size: 16px;
  height: 28px;
  line-height: lh(16,28);
  padding-left: 5px;
  position: relative;
  text-align: left;
  width: 50px;
  z-index: 0;

  &:hover,
  &:focus {
    border-radius: 50%;
    clip-path: none;
    height: 50px;
    padding: 0;
    text-align: center;
    z-index: 2;

    & + .plan__infoCard {
      display: block;
      z-index: 1;
    }
  }

  &:focus {
    border: 1px solid #ffffff;
  }
}

.plan__infoMarker--bgc {
  background-color: #b40606;
}

.plan__infoMarker--position {

  & + .plan__infoCard {
    top: 10px;
  }

  &:hover,
  &:focus {

    & + .plan__infoCard {
      transform: translateY(-100%);
    }
  }
}

.plan__infoCard {
  background-color: #ffffff;
  border-radius: 10px;
  display: none;
  left: -60px;
  padding: 25px 20px;
  position: absolute;
  top: 40px;
  width: 170px;
  z-index: -1;
}

.plan__infoCardTitle {
  color: #06b436;
  font-weight: 600;
  font-size: 16px;
  line-height: lh(16,19);
  margin-bottom: 15px;
  text-transform: uppercase;
}

.plan__infoCardBlock {
  margin-bottom: 10px;

  &:nth-child(2) {

    .plan__infoCardText {
      font-weight: 600;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.plan__infoCardSubtitle {
  color: rgba(40, 41, 46, 0.7);
  font-weight: 300;
  font-size: 12px;
  line-height: lh(12,14);
  margin-bottom: 3px;
}

.plan__infoCardText {
  font-weight: 400;
  font-size: 14px;
  line-height: lh(14,17);
}

.plan__note {
  background: rgba(6, 180, 54, 0.5);
  backdrop-filter: blur(2px);
  border-radius: 0 0 10px 10px;
  min-width: 240px;
  padding: 30px 35px;
  position: absolute;
  right: 215px;
  top: 0;
}

.plan__noteText {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 400;
  font-size: 20px;
  line-height: lh(20,28);
}

.plan__noteNumber {
  color: #e9c878;
  font-weight: 600;
  font-size: 58px;
  line-height: lh(58,58);
  margin: 10px 0 5px;
}

.plan__noteIcon {
  right: -31px;
  bottom: -37px;
}


@mixin plan-576 {

  .plan__img {
    height: 500px;
  }

  .plan__btns {
    right: 16px;
  }

  .plan__note {
    min-width: 150px;
    padding: 12px 15px;
    right: 170px;
  }

  .plan__noteText {
    font-size: 14px;
    line-height: lh(14,20);
  }

  .plan__noteNumber {
    font-size: 40px;
    line-height: lh(40,40);
  }

  .plan__noteIcon {
    bottom: -21px;
    right: -36px;
  }

}