.walls {
  padding: 120px 0;
}

.walls__title {
  margin-bottom: 15px;
  text-align: center;
}

.walls__subtitle {
  margin-bottom: 111px;
  text-align: center;
}

.walls__block {
  display: flex;
  flex-wrap: wrap;
  margin: -50px -50px 50px;

  &:last-child {
    margin-bottom: 0;
  }

  &:nth-child(2n+1) {
    flex-direction: row-reverse;
  }
}

.walls__blockVisual {
  border: 1px solid #ebebeb;
  max-height: 460px;
  margin: 50px;
  width: 700px;
}

.walls__blockContent {
  flex-grow: 1;
  margin: 50px;
  width: 370px;
}

.walls__blockTitle {
  font-weight: 700;
  font-size: 30px;
  line-height: lh(30,42);
  margin: 10px 0 17px;
}

.walls__blockText {
  font-weight: 300;
  font-size: 20px;
  line-height: lh(20,34);

  & + .walls__blockList {
    margin-top: 15px;
  }
}

.walls__blockList {
  font-weight: 300;
  font-size: 18px;
  line-height: lh(18,30);
}


@mixin walls-992 {

  .walls {
    padding: 50px 0;
  }

}


@mixin walls-768 {

  .walls__subtitle {
    margin-bottom: 60px;
  }

}


@mixin walls-576 {

  .walls__title {
    margin-bottom: 5px;
  }

  .walls__subtitle {
    margin-bottom: 40px;
  }

  .walls__block {
    margin: -8px 0 37px;
  }

  .walls__blockVisual {
    margin: 8px 0;
  }

  .walls__blockContent {
    margin: 8px 0;
  }

  .walls__blockTitle {
    font-size: 22px;
    line-height: lh(22,30);
    margin: 0 0 10px;
  }

  .walls__blockText {
    font-size: 16px;
    line-height: lh(16,24);

    & + .walls__blockList {
      margin-top: 10px;
    }
  }

  .walls__blockList {
    font-weight: 300;
    font-size: 14px;
    line-height: lh(14,24);
    padding-left: 14px;
  }

}