.slider {
  height: 700px;
  position: absolute;
  top: 0;
  width: 770px;
}

.slider__item {
  height: 700px;
}

.slider__btns {
  position: absolute;

  button {
    background: url(/assets/src/img/common/arrow.svg) no-repeat center, #007aff;
    border: none;
    border-radius: 50%;
    display: inline-block;
    height: 50px;
    position: relative;
    top: 0;
    transform: none;
    width: 50px;

    &[aria-disabled="true"] {
      opacity: 0.3;
    }

    &:hover,
    &:focus {
      background: url(/assets/src/img/common/arrow.svg) no-repeat center, #007aff;
    }

    &:first-child {
      left: 0;
      transform: rotate(180deg);
    }

    &:last-child {
      right: 0;
      margin-left: 25px;
    }

    &::before {
      content: '';
      display: none;
    }

  }

}


@mixin slider-992 {

  .slider {
    height: 600px;
    width: 670px;
  }

  .slider__item {
    height: 600px;
  }

}


@mixin slider-768 {

  .slider {
    height: 100vw;
    width: 95vw;
  }

  .slider__item {
    height: 100vw;
  }

}


@mixin slider-576 {

  .slider {
    height: 91vw;
    top: auto;
    width: 100vw;
  }

  .slider__item {
    height: 91vw;
  }

  .slider__btns {
    position: relative;

    button {
      height: 40px;
      width: 40px;

      &:last-child {
        margin-left: 15px;
      }
    }
  }

}
