.variants {
  padding: 120px 0;
  position: relative;

  .container {
    overflow: visible;
  }
}

.variants__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: -12px -12px 88px -12px;
}

.variants__title {
  margin: 12px;
}

.variants__note {
  margin: 12px;
  max-width: 400px;
  min-width: 288px;
  padding: 22px 30px;
  position: relative;
  text-align: right;

  svg {
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
  }

  span {
    font-weight: 400;
    font-size: 18px;
    line-height: lh(18,28);
  }
}

.variants__slider {
  display: flex;
  margin-bottom: 100px;
  position: relative;

  &:last-child {
    margin-bottom: 0;
  }
}

.variants__sliderContent {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 700px;
  padding: 20px 85px 0 0;
  width: 585px;
}

.variants__sliderContentTop {
  flex-grow: 1;
  margin-bottom: 10px;
}

.variants__sliderTitle {
  margin-bottom: 25px;
}

.variants__sliderText {
  font-weight: 300;
  font-size: 20px;
  line-height: lh(20,34);
  overflow: hidden;
}

.variants__sliderContentBottom {
  align-items: center;
  display: flex;
  margin-bottom: 50px;
}

.variants__sliderCountry {
  color: #007aff;
  font-family: Neumann, sans-serif;
  font-weight: 400;
  font-size: 45px;
  line-height: lh(45,66);
}

.variants__sliderSeparate {
  background-color: #28292e;
  height: 50px;
  margin: 0 15px;
  opacity: 0.8;
  transform: rotate(15deg);
  width: 1px;
}

.variants__sliderCount {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-weight: 300;
  opacity: 0.8;
}

.variants__sliderCountNum {
  font-size: 32px;
  line-height: lh(32,38);
}

.variants__sliderCountText {
  font-size: 14px;
  line-height: lh(14,16);
}

.variants__sliderBody {
  right: -185px;
  top: 0;
}

.variants__sliderBtns {
  position: relative;
}


@mixin variants-1500 {

  .variants__sliderContentTop {
    margin-bottom: 30px;
  }

  .variants__slider {
    flex-direction: column;
  }

  .variants__sliderContent {
    height: auto;
    margin-bottom: 50px;
    padding: 0;
    width: auto;
  }

  .variants__sliderBody {
    position: relative;
    right: auto;
    top: auto;
  }

}


@mixin variants-992 {

  .variants {
    padding: 50px 0;
  }

}


@mixin variants-576 {

  .variants__header {
    margin: -12px -12px 18px -12px;
  }

  .variants__note {
    padding: 21px 22px;

    span {
      font-size: 15px;
      line-height: lh(15,24);
    }
  }

  .variants__slider {
    margin-bottom: 40px;
  }

  .variants__sliderContentTop {
    margin-bottom: 10px;
  }

  .variants__sliderContent {
    margin-bottom: 25px;
    padding: 0;
  }

  .variants__sliderTitle {
    margin-bottom: 15px;
  }

  .variants__sliderText {
    font-size: 16px;
    line-height: lh(16,24);
  }

  .variants__sliderContentBottom {
    margin-bottom: 10px;
  }

  .variants__sliderCountry {
    font-size: 35px;
    line-height: lh(35,52);
  }

  .variants__sliderSeparate {
    height: 34px;
  }

  .variants__sliderCountNum {
    font-size: 22px;
    line-height: lh(22,26);
  }

  .variants__sliderCountText {
    font-size: 10px;
    line-height: lh(10,12);
  }

  .variants__sliderBody {
    margin: 0 -16px;
  }

}