.see {
  background: url(/assets/src/img/see/bg.jpg) no-repeat center/cover;
  padding: 100px 0;
  position: relative;

  &::before {
    background-color: rgba(55,102,204,0.8);
    bottom: 0;
    content: '';
    display: block;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 0;
  }
}

.see__body {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.see__content {
  margin-bottom: 70px;
  max-width: 770px;
  position: relative;
}

.see__title {
  margin-bottom: 10px;
  text-align: center;
  text-transform: uppercase;
}

.see__subtitle {
  text-align: center;
}

.see__form {
  max-width: 370px;
  position: relative;

  .sendForm__rules {
    color: rgba(255, 255, 255, 0.7);

    a {
      color: rgba(255, 255, 255, 0.7);

      &::after {
        background-color: rgba(255, 255, 255, 0.7);
      }
    }
  }
}


@mixin see-576 {

  .see {
    padding: 50px 0;
  }

  .see__content {
    margin-bottom: 40px;
  }

  .see__title {
    margin-bottom: 5px;
  }

}