@import "../fonts.scss";

.wrapper {
  color: #28292E;
  display: flex;
  font-family: MuseoSansCyrl, Arial, sans-serif;
  flex-direction: column;
  min-height: 100vh;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  img {
    object-fit: cover;
  }

  a:not(.outline),
  input:not(.outline),
  textarea:not(.outline),
  button:not(.outline) {
    outline: none;
  }

  a:not(.text-decoration) {
    text-decoration: none;
  }

  ul:not(.list-style) {
    list-style: none;
  }
}

.container {
  margin: 0 auto;
  max-width: 1210px;
  overflow: hidden;
  padding: 0 20px;
  width: 100%;
}

.container--outer {
  margin: 0 auto;
  max-width: 1540px;
  width: 100%;
}

.main {
  flex-grow: 1;
}


@mixin commmon-576 {

  .container {
    padding: 0 16px;
  }

}