.offer {
  background: #ffffff;
  border-radius: 0 15px 15px 0;
  min-height: 347px;
  margin: -187px 100px -160px 0;
  position: relative;
  padding: 10px 10px 10px 0;
  z-index: 2;
}

.offer__img {
  bottom: 0;
  height: 281px;
  left: 0;
  position: absolute;
  width: 355px;
}

.offer__body {
  border: 1px solid #f0f0f0;
  border-radius: 0 15px 15px 0;
  min-height: 327px;
  padding: 55px 75px 45px 403px;
}

.offer__wrapper {
  display: flex;
  justify-content: flex-end;
  margin: -10px -15px;
}

.offer__line {
  background: linear-gradient(90deg, rgba(233, 200, 120, 0.05) 0%, #E9C878 100%);
  border-radius: 20px;
  flex-grow: 1;
  height: 2px;
  margin: 25px 5px 0 15px;
}

.offer__content {
  flex-shrink: 2;
  max-width: 490px;
  margin: 10px 15px;
}

.offer__title {
  font-weight: 700;
  font-size: 26px;
  line-height: lh(26,34);
  margin-bottom: 20px;
  max-width: 410px;
}

.offer__text {
  color: rgba(40, 41, 46, 0.8);
  font-weight: 400;
  font-size: 20px;
  line-height: lh(20,30);
}

.offer__form {
  margin: 10px 15px;
  max-width: 370px;
}


@mixin offer-1500 {

  .offer {
    border-radius: 0;
    margin: 0;
    min-height: auto;
    padding: 0;
  }

  .offer__body {
    border-radius: 0;
    min-height: auto;
    padding: 50px;
  }

  .offer__wrapper {
    justify-content: center;
  }

  .offer__img {
    display: none;
  }

  .offer__line {
    display: none;
  }

}


@mixin offer-992 {

  .offer__wrapper {
    flex-direction: column;
  }

  .offer__content {
    max-width: none;
  }

  .offer__title {
    max-width: none;
  }

  .offer__form {
    align-self: center;
  }

}


@mixin offer-576 {

  .offer__img {
    display: none;
  }

  .offer__body {
    padding: 50px 16px;
  }

  .offer__title {
    font-size: 22px;
    line-height: lh(22,30);
    margin-bottom: 10px;
    text-align: center;
  }

  .offer__text {
    font-size: 16px;
    line-height: lh(16,24);
    text-align: center;
  }

}